.rightArrow{
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 50%;
    transition: all 0.3s ease-in-out;
}

.rightArrow:hover, .rightArrow:focus{
    background-color: rgb(219, 219, 219);
}